import React from 'react';
import { Keyframes, config } from 'react-spring';
import { PaperCard } from '../paper-card';
import { getRandomInt } from '../../../../../../utils/getRandomInt';
import styles from './styles.module.css';

const Container = Keyframes.Spring({
  initial: {
    transform: 'rotateX(0deg)',
    opacity: 1,
    from: {
      transform: 'rotateX(180deg)',
      opacity: 0
    },
    config: config.gentle
  },
  onVote: async (next, cancel, props) => {
    await next({
      transform: 'rotateX(-180deg)',
      opacity: 0,
      config: config.gentle
    });
    if (props.onFinished) {
      props.onFinished();
    }
  }
});

export class Ballot extends React.Component {
  /* Variable definitions */
  questionCount = getRandomInt(28, 32);
  ballots = [
    {
      category: 'Perkebunan',
      statement: 'Expor kelapa sawit harus dikurangi',
      details: 'Details here...',
      answer: null
    },
    {
      category: 'Lingkungan',
      statement: 'Penggunaan plastik sekali pakai harus di larang',
      details: 'Details here...',
      answer: null
    }
  ];
  currentBallotIndex = getRandomInt(0, this.ballots.length - 1);

  state = {
    ballot: this.ballots[this.currentBallotIndex]
  };

  /* Function definitions */

  vote = async answer => {
    this.props.setStepFn('onVote');
    await this.setState((state, props) => ({
      ballot: {
        ...state.ballot,
        answer
      },
      ballotStyle: { transform: 'rotateX(-180deg)', opacity: 0 }
    }));
  };

  cbOnVote = () => {
    this.props.setStepFn('voted');
  };

  render() {
    return (
      <div className={styles.container}>
        <Container state={this.props.step}>
          {props => (
            <h1 className={styles.header} style={props}>
              Suara 1/
              {this.questionCount}
            </h1>
          )}
        </Container>
        <Container state={this.props.step} onFinished={this.cbOnVote}>
          {props => (
            <div className={styles.ballotContainer} style={props}>
              <PaperCard className={styles.ballotBody}>
                <div className={styles.statement}>
                  <div className={styles.category}>
                    {this.state.ballot.category}
                  </div>
                  <div className={styles.question}>
                    {this.state.ballot.statement}
                  </div>
                </div>

                <div className={styles.vote}>
                  <div>
                    <button
                      className={styles.option}
                      onClick={() => this.vote('agree')}
                    >
                      Setuju
                    </button>
                  </div>
                  <div>
                    <button
                      className={styles.option}
                      onClick={() => this.vote('abstain')}
                    >
                      Netral
                    </button>
                  </div>
                  <div>
                    <button
                      className={styles.option}
                      onClick={() => this.vote('disagree')}
                    >
                      Tidak setuju
                    </button>
                  </div>
                </div>
              </PaperCard>
            </div>
          )}
        </Container>
      </div>
    );
  }
}
